body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	font-family: 'Noto Sans', serif;
}

.appDiv {
	position: fixed;
}

.mainDiv {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
}



#title, #profile {
	display: inline-block;

}

#title {
	/*padding-top: 10%;
	padding-bottom: 10%;*/
	text-align: center;
	font-size: 1.5em;

	line-height: .9;
	padding-left: 10%;
	width: 40%;
}

#title_content {
	display: inline-block;
	text-align: left;
}


#profile {
	font-size: 0.9em;
	margin-left: 5%;

}

#projects {
	padding-top: 5%;
	margin-left: auto;
	text-align: center;
}

#projects_container {
	text-align: left;
	display: inline-block;
	width: auto;
}

.project_item {
	margin-bottom: 2em;
}

#tagline {
	font-size: .8em;
}

.header {
	display: block;
	font-size: 1.2em;
	line-height: 0.5;
	padding-bottom: 0.1em;
}

.item {
	padding-left: 1%;
	padding: 0px;
	line-height: 0.2;
}


.tab {
	padding-left: 1%;
	display: block;
	margin-left: 2em;
	padding-bottom: 0em;
	font-size: .9em;
}
.palette {
	display: inline-block;
	float: left;
	z-index: 1;
}

.canvas {
	position:relative;
	top:20;
	left:15;
	z-index: 0;


}
.label {
	font-size: 1em;
}

.right {
	float: right;
	margin-right: 5%;
}
